import React from 'react';
import { Typography, AppBar, Button, Card, CardActions, CardContent,CardMedia, CssBaseline, Grid, Toolbar, Container } from '@material-ui/core';
import SportsEsportsIcon from '@material-ui/icons/SportsEsports';
import useStyles from './styles';

const App = () => {
    const classes = useStyles();
    return(
        <>
            <CssBaseline />
            <AppBar position="relative" style={{ background: '#2E3B55' }}>
                <Toolbar>
                    <SportsEsportsIcon className={classes.icon}/>
                    <Typography variant="h6">
                        Pokédex
                    </Typography>
                </Toolbar>
            </AppBar>
            <main>
                <div className={classes.container}>
                    <Container maxWidth="sm">
                        <Typography variant="h2" align="center" color="textPrimary" gutterBottom>
                            Pokédex
                        </Typography>
                        <Typography variant="h6" align="center" color="textSecondary" paragraph> 
                            Hello, folks! Everyone of you might have been a pokemon fan at some point of time in their life and would have wished to be a Pokemon master xD. Well this page has nothing to do with being a pokemon master or about how to catch a pokemon. These are just the pokemons that are my favourite and I feel they can dominate the rest in an one on one battle.   
                        </Typography>
                        <div className={classes.buttons}>
                            <Grid container spacing={2} justify="center">
                                <Grid item>
                                    <Button variant="contained" color="primary">
                                        Featured
                                    </Button>
                                </Grid>
                                <Grid item>
                                    <Button variant="outlined" color="primary">
                                        Collection
                                    </Button>
                                </Grid>
                            </Grid>
                        </div>
                    </Container>
                </div>
                <Container className={classes.cardGrid} maxWidth="md">
                    <Grid container spacing={4}>
                        <Grid item xs={12} sm={6} md={4}>
                            <Card className={classes.card}>
                                <CardMedia 
                                    className={classes.cardMedia} 
                                    image="https://assets.pokemon.com/assets/cms2/img/pokedex/detail/006.png" 
                                    title="Charizard"/>
                                <CardContent className={classes.cardContent}>
                                    <Typography gutterBottom variant="h5">
                                        Charizard
                                    </Typography>
                                    <Typography>
                                        It spits fire that is hot enough to melt boulders. It may cause forest fires by blowing flames.
                                    </Typography>
                                    <CardActions>
                                        <Button size="small" color="primary">View</Button>
                                        <Button size="small" color="primary">Edit</Button>
                                    </CardActions>
                                </CardContent>
                            </Card>
                        </Grid>
                        <Grid item xs={12} sm={6} md={4}>
                            <Card className={classes.card}>
                                <CardMedia 
                                    className={classes.cardMedia} 
                                    image="https://assets.pokemon.com/assets/cms2/img/pokedex/full/026.png" 
                                    title="Raichu"/>
                                <CardContent className={classes.cardContent}>
                                    <Typography gutterBottom variant="h5">
                                        Raichu
                                    </Typography>
                                    <Typography>
                                        Its long tail serves as a ground to protect itself from its own high-voltage power.
                                    </Typography>
                                    <CardActions>
                                        <Button size="small" color="primary">View</Button>
                                        <Button size="small" color="primary">Edit</Button>
                                    </CardActions>
                                </CardContent>
                            </Card>
                        </Grid>
                        <Grid item xs={12} sm={6} md={4}>
                            <Card className={classes.card}>
                                <CardMedia 
                                    className={classes.cardMedia} 
                                    image="https://assets.pokemon.com/assets/cms2/img/pokedex/detail/009.png" 
                                    title="Blastoise"/>
                                <CardContent className={classes.cardContent}>
                                    <Typography gutterBottom variant="h5">
                                        Blastoise
                                    </Typography>
                                    <Typography>
                                        It crushes its foe under its heavy body to cause fainting. In a pinch, it will withdraw inside its shell.
                                    </Typography>
                                    <CardActions>
                                        <Button size="small" color="primary">View</Button>
                                        <Button size="small" color="primary">Edit</Button>
                                    </CardActions>
                                </CardContent>
                            </Card>
                        </Grid>
                        <Grid item xs={12} sm={6} md={4}>
                            <Card className={classes.card}>
                                <CardMedia 
                                    className={classes.cardMedia} 
                                    image="https://assets.pokemon.com/assets/cms2/img/pokedex/detail/487.png" 
                                    title="Giratina"/>
                                <CardContent className={classes.cardContent}>
                                    <Typography gutterBottom variant="h5">
                                        Giratina
                                    </Typography>
                                    <Typography>
                                        This Pokémon is said to live in a world on the reverse side of ours, where common knowledge is distorted and strange.
                                    </Typography>
                                    <CardActions>
                                        <Button size="small" color="primary">View</Button>
                                        <Button size="small" color="primary">Edit</Button>
                                    </CardActions>
                                </CardContent>
                            </Card>
                        </Grid>
                        <Grid item xs={12} sm={6} md={4}>
                            <Card className={classes.card}>
                                <CardMedia 
                                    className={classes.cardMedia} 
                                    image="https://assets.pokemon.com/assets/cms2/img/pokedex/detail/250.png" 
                                    title="Onix"/>
                                <CardContent className={classes.cardContent}>
                                    <Typography gutterBottom variant="h5">
                                        Ho-Oh
                                    </Typography>
                                    <Typography>
                                        Ho-Oh’s feathers glow in seven colors depending on the angle at which they are struck by light. These feathers are said to bring happiness to the bearers. This Pokémon is said to live at the foot of a rainbow.
                                    </Typography>
                                    <CardActions>
                                        <Button size="small" color="primary">View</Button>
                                        <Button size="small" color="primary">Edit</Button>
                                    </CardActions>
                                </CardContent>
                            </Card>
                        </Grid>
                        <Grid item xs={12} sm={6} md={4}>
                            <Card className={classes.card}>
                                <CardMedia 
                                    className={classes.cardMedia} 
                                    image="https://assets.pokemon.com/assets/cms2/img/pokedex/detail/384.png" 
                                    title="Rayquaza"/>
                                <CardContent className={classes.cardContent}>
                                    <Typography gutterBottom variant="h5">
                                        Rayquaza
                                    </Typography>
                                    <Typography>
                                        Rayquaza is said to have lived for hundreds of millions of years. Legends remain of how it put to rest the clash between Kyogre and Groudon.
                                    </Typography>
                                    <CardActions>
                                        <Button size="small" color="primary">View</Button>
                                        <Button size="small" color="primary">Edit</Button>
                                    </CardActions>
                                </CardContent>
                            </Card>
                        </Grid>
                        <Grid item xs={12} sm={6} md={4}>
                            <Card className={classes.card}>
                                <CardMedia 
                                    className={classes.cardMedia} 
                                    image="https://assets.pokemon.com/assets/cms2/img/pokedex/detail/150.png" 
                                    title="Mewtwo"/>
                                <CardContent className={classes.cardContent}>
                                    <Typography gutterBottom variant="h5">
                                        Mewtwo
                                    </Typography>
                                    <Typography>
                                        Its DNA is almost the same as Mew’s. However, its size and disposition are vastly different.
                                    </Typography>
                                    <CardActions>
                                        <Button size="small" color="primary">View</Button>
                                        <Button size="small" color="primary">Edit</Button>
                                    </CardActions>
                                </CardContent>
                            </Card>
                        </Grid>
                        <Grid item xs={12} sm={6} md={4}>
                            <Card className={classes.card}>
                                <CardMedia 
                                    className={classes.cardMedia} 
                                    image="https://assets.pokemon.com/assets/cms2/img/pokedex/detail/068.png" 
                                    title="Machamp"/>
                                <CardContent className={classes.cardContent}>
                                    <Typography gutterBottom variant="h5">
                                        Machamp
                                    </Typography>
                                    <Typography>
                                        It quickly swings its four arms to rock its opponents with ceaseless punches and chops from all angles.
                                    </Typography>
                                    <CardActions>
                                        <Button size="small" color="primary">View</Button>
                                        <Button size="small" color="primary">Edit</Button>
                                    </CardActions>
                                </CardContent>
                            </Card>
                        </Grid>
                        <Grid item xs={12} sm={6} md={4}>
                            <Card className={classes.card}>
                                <CardMedia 
                                    className={classes.cardMedia} 
                                    image="https://assets.pokemon.com/assets/cms2/img/pokedex/detail/095.png" 
                                    title="Onix"/>
                                <CardContent className={classes.cardContent}>
                                    <Typography gutterBottom variant="h5">
                                        Onix
                                    </Typography>
                                    <Typography>
                                        As it digs through the ground, it absorbs many hard objects. This is what makes its body so solid.
                                    </Typography>
                                    <CardActions>
                                        <Button size="small" color="primary">View</Button>
                                        <Button size="small" color="primary">Edit</Button>
                                    </CardActions>
                                </CardContent>
                            </Card>
                        </Grid>
                    </Grid>
                </Container>
            </main>
            <footer className={classes.footer}>
                <Typography variant="h6" align="center" gutterBottom>
                        Thanks for Visiting!
                </Typography>
                <Typography variant="subtitle1" align="center" color="textSeconary">
                    Designed <span role="img">🎨</span> and made by Ataj using React and Material UI.<span role="img">⚡</span>
                </Typography>
            </footer>
        </>
    )
}

export default App;